import React from 'react';
import '../css/headers.css';

export default (props) => {
  return(
    <div>
        <div className="headWrp">
            <div className="innerWrp">
                <img src={props.img} alt="avatar" />
            </div>        
        </div>
        <p>{props.name}</p>
    </div>
  )
};
