import React, { useState } from "react";
import { Link } from "react-router-dom";

import "../css/navs.css";
import Headers from '../components/Headers'

import homeIcon from "../imgs/home.png";
import articleIcon from "../imgs/article.png";
import aboutIcon from "../imgs/about.png";
import meIcon from "../imgs/me.png";
import headPic from "../imgs/cat.gif";

function Navs() {
    const [switchs,setSwitchs] = useState(true)
    function clsNav() {
        setSwitchs(true)
    }
    function opnNav() {
        setSwitchs(false)
    }
    return (
        <div className="navWrp">
            <div className={switchs ? "navs hideNav" : "navs"}>
                <span
                    className={!switchs ? "navBtn hideIcon" : "navBtn"}
                    onClick={opnNav}
                />
                <div style={{ height: "100%" }} onClick={clsNav}>
                    <div className="infosWrp">
                        <Headers img={headPic} name={'X Man'} /> 
                    </div>

                    <div className="links">
                        <Link to="/home">
                            <img src={homeIcon} alt="homeIcon" />
                            <strong> Home </strong>
                        </Link>
                    </div>
                    <div className="links">
                        <Link to="/article">
                            <img src={articleIcon} alt="articleIcon" />
                            <strong> Article </strong>
                        </Link>
                    </div>
                    <div className="links">
                        <Link to="/about">
                            <img src={aboutIcon} alt="aboutIcon" />
                            <strong> About </strong>
                        </Link>
                    </div>
                    <div className="links">
                        <Link to="/me">
                            <img src={meIcon} alt="meIcon" />
                            <strong> me </strong>
                        </Link>
                    </div>

                    <p className="supports">Jash8 technical support</p>
                </div>
            </div>
        </div>
    );
}

export default Navs;
